.intro {
    background: url(../img/Femida-Retina3.jpg) no-repeat;
    background-size: cover;
    background-position: center center;
    background-attachment: fixed;
    text-align: center;
    color: #fff;
    min-height: 650px;
    
    &_wrap {
        padding: 80px 0 20px;
        
        &__title {
            font-size: 52px;
            font-weight: 700;
            line-height: 60px;
            padding: 10px;
            
            .xs-block({
                font-size: 36px;
            });
            .sm-block({
                line-height: 1.2;
                font-size: 42px;
            });
        }
        &__descr1 {
            padding-top: 25px;
            font-size: 32px;
            line-height: 1.2;
            font-weight: 400;
            
            .sm-block({
                line-height: 1.2;
                font-size: 24px;
            });
        }
        &__descr2 {
            padding-top: 25px;
            font-size: 24px;
            line-height: 1.2;
            font-weight: 400;
            
            .sm-block({
                line-height: 1.2;
                font-size: 18px;
            });
        }
        &__btn {
            display: inline-block;
            margin: 80px auto 20px;
            font-size: 16px;
            text-transform: uppercase;
            line-height: 21px;
            font-weight: 600;
            letter-spacing: 1px;
            padding: 20px 65px;
            border: 1px solid #eee;
            background-color: #fff;
            color: #000;
            transition: .3s all;
            .xs-block({
                padding: 15px 40px;
                font-size: 11px;
                margin-top: 60px;
            });

            &:hover {
                background: #a64b83;
                color: #fff;
                border: 1px solid #a64b83;
            }
            
            .sm-block({
                line-height: 1.2;
                font-size: 18px;
            });
        }
        
    }
}
.intro-second {
    background-color: @fiolet;
    color: #fff;
    padding: 35px 5px;
    max-width: 960px;
    margin: -50px auto 0;
    text-align: center;
    
    .xs-block({
        padding: 10px;
        text-align: left;
        margin: auto;
    });
    
    &__text {
        margin: 0 auto;
        max-width: 650px;
        font-weight: 600;
        font-size: 24px;
        text-transform: uppercase;
        
        .sm-block({
           font-size: 18px;
        });
    }
}