.how {
    margin-top: 50px;
    &_title {
        text-align: center;
    }
    &_items {
        margin-top: 55px;
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;     
        
        .lg-block({
            position: static;
            text-align: center;
        });
    }     
        &__text {
            position: absolute;
            color: #32252d;
            font-size: 20px;
            font-weight: 600;
            max-width: 500px;
            
            .lg-block({
                position: static;
                padding: 20px 5px;
            });
        }
    &__text1 {
        top: 16px; left: 60px;
    }
    &__text2 {
        top: 152px; right: 0;
    }
    &__text3 {
        top: 302px; left: 60px;
    }
    &__text4 {
        top: 440px; right: 0;
    }
    &__text5 {
        top: 603px; left: 60px;
    }
    &__text6 {
        top: 749px; right: 0;
    }
    &_icon {
        position: relative;
        width: 70px;
        height: 70px;
        background: @fiolet;
        border-radius: 100%;
        transition: all ease-in-out 0.4s;
        margin-bottom: 79px;
        
        .lg-block({
            
        });
        
        .icon {
            display: block;
            width: 35px;
            height: 35px;
            position: absolute;
            top: 50%; left: 50%;
            transform: translate(-50%,-50%);
        }
        .icon-search {
            background: url(../img/searching-tool-outline.svg) no-repeat;
            background-position: top center;
            background-size: 35px 35px;
        }
        .icon-case {
            background: url(../img/work-briefcase.svg) no-repeat;
            background-position: top center;
            background-size: 35px 35px;
        }
        .icon-man {
            background: url(../img/businessman.svg) no-repeat;
            background-position: top center;
            background-size: 35px 35px;
        }
        .icon-hummer {
            background: url(../img/hammer.svg) no-repeat;
            background-position: top center;
            background-size: 35px 35px;
        }
        .icon-doc {
            background: url(../img/edit-document.svg) no-repeat;
            background-position: top center;
            background-size: 35px 35px;
        }
        .icon-win {
            background: url(../img/winner.svg) no-repeat;
            background-position: top center;
            background-size: 35px 35px;
        }
        
    
        &::before {
            background: url(../img/ver-str.png) repeat scroll center center;
            content: "";
            height: 70px;
            margin: 5px 0;
            position: absolute;
            right: 30px;
            top: 100%;
            width: 10px;
        }
        
        &:hover {
            background: #a64b83;
        }
        
        &::after {
            border: 1px solid #a64b83;
            bottom: -8px;
            content: "";
            left: -8px;
            margin: auto;
            padding: 0;
            position: absolute;
            right: -8px;
            top: -8px;
            border-radius: 100%;
            opacity: 0;
            visibility: hidden;
            transition: all ease-in-out 0.4s;
            -webkit-transition: all ease-in-out 0.4s;
        }
        &:hover::after {
            opacity: 1;
            visibility: visible;
        }
        
    }
}