.lead {
    background: url(../img/banner.png) no-repeat;
    background-position: center center;
    background-size: cover;
    background-attachment: fixed;
    color: #fff;
    margin-top: 50px;
    text-align: center;
    padding: 25px 0;
    
    &_title {
        color: #fff;
        padding: 20px 5px;
    }
    &_text {
        text-align: center;
        margin: 0 auto;
        font-size: 20px;
        padding: 10px 5px;
        max-width: 650px;
    }
    &_btn {
        display: inline-block;
        margin: 25px auto 20px;
        font-size: 11px;
        text-transform: uppercase;
        line-height: 19px;
        font-weight: 600;
        letter-spacing: 1px;
        padding: 15px 45px;
        border: 1px solid #eee;
        background-color: #fff;
        color: #000;
        transition: .3s all;
        .xs-block({
            padding: 10px 15px;
            font-size: 11px;
        });
        
        &:hover {
            background: #a64b83;
            color: #fff;
            border: 1px solid #a64b83;
        }
    }
}