.btn-up {
  cursor: pointer;
  position: fixed;
  bottom: -50px;
  right: 50px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 2px solid #49273c;
  background: #49273c;
  z-index: 100;
  outline: 0!important;
  -webkit-transition: .4s;
  -o-transition: .4s;
  transition: .4s;
}

.btn-up::after {
  content: '';
  width: 10px;
  height: 10px;
  border-top: 2px solid #fff;
  border-left: 2px solid #fff;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
  position: absolute;
  top: 4px;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
}

.btn-up:hover::before {
  content: "";
  position: absolute;
  border: 1px solid #49273c;
  left: -10px;
  right: -10px;
  top: -10px;
  bottom: -10px;
  border-radius: 50%;
  -webkit-animation: pulse 1.8s linear infinite;
  animation: pulse 1.8s linear infinite;
}

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
    opacity: 0;
  }

  50% {
    opacity: 1;
  }

  100% {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
    opacity: 0;
  }
}

@keyframes pulse {
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
    opacity: 0;
  }

  50% {
    opacity: 1;
  }

  100% {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
    opacity: 0;
  }
}

.btn-up.active {
  bottom: 50px;
}

.overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.8);
