}

.popup {
  position: absolute;
  width: 400px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  padding: 40px;
  box-sizing: border-box;
  background-color: #49273c;
  color: #fff;
  text-align: center;
}

.popup_title {
  font-size: 32px;
  font-weight: 600;
  padding: 10px 0;
}

.popup_descr {
  margin: 10px auto 25px;
  font-weight: 300;
}

.popup_input {
  display: block;
  margin: 15px auto;
  background-color: transparent;
  padding: 15px 45px;
  border-bottom: 1px solid #fff;
  color: #fff;
}

.popup_btn {
  cursor: pointer;
  display: inline-block;
  margin: 25px auto 15px;
  font-size: 14px;
  text-transform: uppercase;
  line-height: 19px;
  font-weight: 600;
  letter-spacing: 1px;
  padding: 15px 45px;
  border: 1px solid #fff;
  background-color: transparent;
  color: #fff;
  transition: .3s all;
}

.popup_btn:hover {
  background: #a64b83;
  border: 1px solid #a64b83;
}

.close-popup {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 23px;
  height: 23px;
  cursor: pointer;
}

.close-popup:before {
  content: '';
  background-color: #fff;
  position: absolute;
  height: 1px;
  width: 31px;
  top: 11px;
  left: -4px;
  transform: rotate(-45deg);
}

.close-popup:after {
  content: '';
  background-color: #fff;
  position: absolute;
  height: 1px;
  width: 31px;
  top: 11px;
  transform: rotate(45deg);
  left: -4px;
}

.success {
  display: none;
  color: #000;
  font-size: 22px;
  text-align: center;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10;
  width: 100%;
  height: 100%;
  opacity: 1;
  text-transform: uppercase;
  font-weight: bold;
}

.success span {
  margin: auto;
}

.success span ~ * {
  transition: opacity 2s ease, filter 2s ease;
}

.success span ~ * span,
.success span ~ * input,
.success span ~ * textarea,
.success span ~ * select {
  transition: opacity 2s ease, filter 2s ease;
}

.success span.active ~ * {
  opacity: .75;
  filter: blur(5px);
}

.success span.active ~ * span,
.success span.active ~ * input,
.success span.active ~ * textarea,
.success span.active ~ * select {
  opacity: .75;
  filter: blur(5px);
}

