  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
}

.main-header_bottom .btn_mnu.active .btn_row:first-child {
  -webkit-transform: rotate(-135deg);
  -ms-transform: rotate(-135deg);
  -o-transform: rotate(-135deg);
  transform: rotate(-135deg);
  top: 6px;
}

.main-header_bottom .btn_mnu.active .btn_row:last-child {
  -webkit-transform: rotate(-225deg);
  -ms-transform: rotate(-225deg);
  -o-transform: rotate(-225deg);
  transform: rotate(-225deg);
  top: -6px;
}

.main-header_bottom .btn_mnu.active .btn_row:nth-child(2) {
  opacity: 0;
}

.main-header_bottom .main-nav.active {
  opacity: 1;
}

