.form {
    margin-top: 50px;
    background: url(../img/consult.jpg) no-repeat;
    background-size: cover;
    background-position: center center;
    color: #fff;
    text-align: center;
    
    &_title {
        padding-top: 50px;
        color: #fff;
    }
    &_descr {
        margin: 25px auto 50px;
        max-width: 750px;
        font-weight: 300;
        line-height: 27px;
    }
    textarea {
        border: none;
    }
    input,textarea {
        display: block;
        margin: 15px auto;
        background-color: transparent;
        padding: 15px 45px;
        border-bottom: 2px solid @fiolet;
        color: #fff;
    }
    button {
        cursor: pointer;
        display: inline-block;
        margin: 25px auto 50px;
        font-size: 14px;
        text-transform: uppercase;
        line-height: 19px;
        font-weight: 600;
        letter-spacing: 1px;
        padding: 15px 45px;
        border: 1px solid #eee;
        background-color: #fff;
        color: #000;
        transition: .3s all;

        &:hover {
            background: #a64b83;
            color: #fff;
            border: 1px solid #a64b83;
        }
    }
    input::placeholder { 
        color: #f0f0f0; 
    } 
    textarea::placeholder { 
        color: #f0f0f0; 
    } 
}