.main-header {
    
    &_top {
        background-color: #222;
        color: #fff;
        padding: 12px 0;
        text-align: center;
        
        &__logo {
            padding: 0 5px;
        }
        
        &__wrap {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        
        &__btn {
            display: inline-block;
            font-size: 14px;
            text-transform: uppercase;
            line-height: 18px;
            font-weight: 600;
            letter-spacing: 1px;
            padding: 10px 35px;
            border: 2px solid #a64b83;
            background-color: transparent;
            color: #fff;
            transition: .3s all;
            .xxs-block({
                padding: 10px 25px;
                font-size: 12px;
            });

            &:hover {
                background: #a64b83;
            }
        
    }
}
    
    &_bottom {  
        padding: 20px 0;
        background-color: #fff;
        
        .lg-block({
            padding: 35px 0;
        });
        
        .nav-wrap {
            position: relative; 
        }
        
        .main-nav {
            display: flex;
            align-items: center;
            justify-content: center;
            transition: .3s all;
            
            .lg-block({
                opacity: 0;
                flex-direction: column;
                position: absolute;
                background-color: #222;
                color: #fff;
                width: 280px;
                position: absolute;
                top: 20px; right: 0;
                padding: 5px 0;
            });
            
            &_item {
                display: block;
                font-size: 11px;
                text-transform: uppercase;
                line-height: 19px;
                font-weight: 600;
                letter-spacing: 1px;
                padding: 5px;
                margin: 0 15px;
                
                .lg-block({
                    padding: 8px 5px;
                });
                
                &:hover {
                    .lg-block({
                        border-bottom: 1px solid #fff;
                    });
                }
                
                &::after {
                    display:block;
                    content: '';
                    border-bottom: 1px solid #2d2d2d;  
                    -webkit-transform: scaleX(0);  
                    -ms-transform: scaleX(0);  
                    transform: scaleX(0);  
                    -webkit-transform-origin: 100% 50%;  
                    -ms-transform-origin: 100% 50%;  
                    transform-origin: 100% 50%;
                    -webkit-transition: -webkit-transform 450ms ease-in-out;
                    transition: -webkit-transform 450ms ease-in-out;
                    -o-transition: transform 450ms ease-in-out;
                    transition: transform 450ms ease-in-out;
                    transition: transform 450ms ease-in-out, -webkit-transform 450ms ease-in-out;
                    
                    .lg-block({
                       display: none;
                    });
                }
                
                &:hover::after {
                    -webkit-transform: scaleX(1);
                    -ms-transform: scaleX(1);
                    transform: scaleX(1);
                    -webkit-transform-origin: 0% 50%;
                    -ms-transform-origin: 0% 50%;
                    transform-origin: 0% 50%;
                }
            }
        }
        .btn_mnu {
            opacity: 0;
            width: 50px;
            height: 50px;
            display: -webkit-flex;
            display: -moz-flex;
            display: -ms-flex;
            display: -o-flex;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            background-color: transparent;
            cursor: pointer;
            position: absolute;
            right: 0; top: -25px;
            
            .lg-block({
                opacity: 1;
            });
        }
        .btn_row {
            height: 2px;
            width: 60%;
            margin: 2px 0;
            background-color: #000;
            position: relative;
            -webkit-transition: all .5s;
            -o-transition: all .5s;
            transition: all .5s;
        }
        .btn_mnu.active .btn_row:first-child {
            -webkit-transform: rotate(-135deg);
            -ms-transform: rotate(-135deg);
            -o-transform: rotate(-135deg);
            transform: rotate(-135deg);
            top: 6px;
        }
        .btn_mnu.active .btn_row:last-child {
            -webkit-transform: rotate(-225deg);
            -ms-transform: rotate(-225deg);
            -o-transform: rotate(-225deg);
            transform: rotate(-225deg);
            top: -6px;
        }
        .btn_mnu.active .btn_row:nth-child(2) {
            opacity: 0;
        }
        .main-nav.active {
            opacity: 1;
        }
    }
}
