.men {
    background: url(../img/man.png) no-repeat;
    background-position: center right;
    background-size: contain;
    padding: 30px 50px 0px;
    margin-top: 50px;
    
    .md-block({
        background: none;
        padding: 30px 0 0 ;
    });
    
    &_title {
        max-width: 750px;
        padding: 0px 5px 30px;
        
        .lg-block({
            max-width: 650px;
        });
        .md-block({
            max-width: 750px;
            text-align: center;
            margin: 0 auto;
        });
    }
    &_text {
        font-size: 20px;
        padding: 15px 5px;
        padding-left: 45px;
        max-width: 650px;
        position: relative;
        
        .lg-block({
            max-width: 500px;
        });
        .md-block({
            max-width: 650px;
            margin: 0 auto;
        });
        
        &::after {
            content: '';
            background: url(../img/right.svg) no-repeat;
            background-size: 25px 25px;
            background-position: center center;
            position: absolute;
            top: 20px; left: 5px;
            width: 25px;
            height: 25px;    
        } 
        
        &:last-child {
            padding-bottom: 50px;
        }
        
    }
}
.men-second {
    background: url(../img/desk.png) no-repeat;
    background-position: center center;
    background-size: cover;
    background-attachment: fixed;
    color: #fff;
    text-align: center;
    padding: 60px 0 40px;
    
    &_text {
        text-align: center;
        margin: 0 auto;
        font-size: 20px;
        padding: 10px 5px;
        max-width: 650px;
    }
    &_btn {
        display: inline-block;
        margin: 25px auto 20px;
        font-size: 11px;
        text-transform: uppercase;
        line-height: 19px;
        font-weight: 600;
        letter-spacing: 1px;
        padding: 15px 45px;
        border: 1px solid #eee;
        background-color: #fff;
        color: #000;
        transition: .3s all;

        &:hover {
            background: #a64b83;
            color: #fff;
            border: 1px solid #a64b83;
        }
    }
}