.debt {
    margin-top: 50px;
    text-align: center;

    &_descr {
        margin: 25px auto 50px;
        max-width: 750px;
        font-weight: 300;
        color: #666;
        line-height: 27px;
    }
    &_items {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: flex-start;
        margin: auto;
    }
    &_item {
        .col();
        .size(4);
        .size-sm(6);
        .size-xs(10);
        margin: 15px 0;
        
        &__title {
            font-weight: 300;
            font-size: 20px;
            max-width: 250px;
            margin: 5px auto 10px;
        }
        &__p {
            margin: 0 auto;
            max-width: 300px;
            font-weight: 300;
            color: #666;
            line-height: 27px;
        }
        
    }
}
.faq {
    margin-top: 50px;
    
    h2 {
        text-align: center;
    }
    .faq-block {
        width: 600px;
        height: 100px;
        background-color: tomato;
        margin: 35px auto 0;
    }
}