.contact {
    margin-top: 50px;
    padding-bottom: 35px;
    min-height: 450px;
    
    &_wrap {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        align-items: center;
    }
    &_text {
        .col();
        .size(4);
        .size-sm(12);
        padding-bottom: 25px;
    }
    &_maps {
        .col();
        .size(8);
        .size-sm(12);
    }
    
    &_ul {
        margin-top: 30px;
    }
    &_li {
        margin: 10px 0;
        padding-left: 25px;
        position: relative;
        font-weight: 300;
        font-size: 18px;
        transition: .1s all;
        
        &::after {
            content: '';
            position: absolute;
            top: 6px;
            left: 0;
            width: 15px;
            height: 15px;
        }
        
        &:hover {
            color: @fiolet;
        }
    }
    .li-address {
        &::after {
            background: url(../img/icons-address.png) no-repeat;
            background-size: cover;
            background-position: center center;
        }
    }
    .li-email {
        &::after {
            background: url(../img/icon-mail.png) no-repeat;
            background-size: cover;
            background-position: center center;   
        }
    }
    .li-site {
        &::after {
            background: url(../img/icon-comp.png) no-repeat;
            background-size: cover;
            background-position: center center;   
        }
    }
    .li-tel {
        font-size: 24px; 
        letter-spacing: 1px;
        font-weight: 300;
        padding: 10px 0;
    }
    .li-btn {
        margin: 10px 0;
        padding: 10px 0;
        
    }
}