.footer {
    padding: 15px 0;
    background-color: #222;
    color: #fff;
    
    &__wrap {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    
    &__logo {
        padding: 0 5px;
    }

    &__btn {
        display: inline-block;
        font-size: 14px;
        text-transform: uppercase;
        line-height: 18px;
        font-weight: 600;
        letter-spacing: 1px;
        padding: 10px 35px;
        border: 2px solid #a64b83;
        background-color: #a64b83;
        color: #fff; 
        transition: .3s all;
        .xxs-block({
           padding: 10px 25px;
            font-size: 12px;
        });

        &:hover {
            background: #fff;
            color: #a64b83;
        }
    }
    .social {
        .xs-block({
            display: none;
        });
        
        &_item {
            margin: 0 7px;
        }
    }
}