.about {
    margin-top: 50px;
    
    &_title {
        text-align: center;
    }
    &_items {
        margin-top: 80px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: flex-start;
        padding: 0 20px;
    }
    &_item {
        .col();
        .size(3);
        .size-md(6);
        .size-sm(8);
        .size-xs(12);
        position: relative;
        margin: 60px auto 0;
        max-width: 400px;
        font-weight: 300;
        color: #666;
        line-height: 27px;
        padding-bottom: 25px;
        
        .md-block({
            justify-content: center;
            align-items: center;
        });
    
        &::after {
            content: '';
            font-size: 52px;
            color: @fiolet;
            position: absolute;
            top: -60px; left: 0;
            z-index: -1;
            font-weight: 700;
            
            .sm-block({
                left: 50%;
                transform: translate(-50%);
            });
        }
    }
    &_item1 {
        
        &::after {
            content: '01';
        }
    }
    &_item2 {

        &::after {
            content: '02';
        }
    }
    &_item3 {

        &::after {
            content: '03';
        }
    }
    &_item4 {

        &::after {
            content: '04';
        }
    }
}