.if {
    margin-top: 50px;
    &_title {
        padding: 0 25px;
        
        .xs-block({
            text-align: center;
        });
    }
    &_wrap {
        display: flex;
        justify-content: space-between;
    }
    
    &_img {
        padding: 20px;
        border: 1px solid #eee;
        
        .sm-block({
           display: none;
        });
        .md-block({
            border: none;
        });
        
        img {
            max-height: 550px;
        }
    }
    &_text {
        margin-left: auto;
    }
    &_items {
         padding: 10px 25px;   
    }
    &_item {
        padding-left: 35px;
        margin: 10px 0;
        position: relative;
    
        &::after {
            content: '';
            position: absolute;
            top: 12px; left: 5px;
            width: 15px;
            height: 2px;
            background-color: @fiolet;
        }
    }
}