.minimal {
    margin-top: 50px;
    background-color: #f0f0f0;
    padding-bottom: 25px;
    
    &_title {
        .xs-block({
           text-align: center;
        });
    }
    
    &_wrap {
        display: flex;
        padding-top: 50px;
        justify-content: space-around;
        
        .md-block({
            flex-wrap: wrap;
            padding-top: 15px;
        });
    }
    &_text {
        padding-top: 70px;
        .md-block({
            order: -1;
        });
    }
    &_img {
        height: 400px;
        .sm-block({
            height: 320px;
        });
        .xs-block({
            height: 250px;
        });
    }
    &_p {
        padding: 0 5px;
        margin-top: 25px;
        font-weight: 300;
        line-height: 27px;
        font-size: 15px;
        color: #777;
        max-width: 550px;
    }
}