.reset();
.container {
    .container();
}
html {
    font-size: 16px;
}
a {
    text-decoration: none;
    color: inherit;
}
a:hover {
    text-decoration: none;
}
body {
    font-family: 'Open Sans', sans-serif;
}
ul,li {
    margin: 0;
    padding: 0;
}
li {
    list-style: none;
}
:active, :hover, :focus {
    outline: 0;
    outline-offset: 0;
}
input {
    border: none;
}
input::placeholder { 
    color: #fff; 
} 
textarea::placeholder { 
    color: #fff; 
} 

@fiolet: #49273c;

.section-title {
    color: #2a1d1f;
    font-size: 36px;
    line-height: 40px;
    font-weight: 600;
    
    .xs-block({
        font-size: 32px;
    });
}
.btn-up {
    cursor: pointer;
    position: fixed;
    bottom: -50px;
    right: 50px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: 2px solid @fiolet;
    background: @fiolet;
    z-index: 100;
    outline: 0!important;
    -webkit-transition: .4s;
    -o-transition: .4s;
    transition: .4s;
    .xxs-block({
        right: 25px;
    });
}
.btn-up::after {
    content: '';
    width: 10px;
    height: 10px;
    border-top: 2px solid #fff;
    border-left: 2px solid #fff;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
    position: absolute;
    top: 4px; bottom: 0;
    right: 0; left: 0;
    margin: auto;
}
.btn-up:hover::before {
    content: "";
    position: absolute;
    border: 1px solid @fiolet;
    left: -10px; right: -10px;
    top: -10px; bottom: -10px;
    border-radius: 50%;
    -webkit-animation: pulse 1.8s linear infinite;
    animation: pulse 1.8s linear infinite;
}
@-webkit-keyframes pulse {
    0% {
        -webkit-transform: scale(0.5);
        transform: scale(0.5);
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        -webkit-transform: scale(1.2);
        transform: scale(1.2);
        opacity: 0;
    }
}
@keyframes pulse {
    0% {
        -webkit-transform: scale(0.5);
        transform: scale(0.5);
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        -webkit-transform: scale(1.2);
        transform: scale(1.2);
        opacity: 0;
    }
}
.btn-up.active {
    bottom: 50px;
}

.overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, .8);
    display: none;
}

.popup {
    position: absolute;
    width: 400px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    padding: 40px;
    box-sizing: border-box;
    background-color: @fiolet;
    color: #fff;
    text-align: center;
    
    &_title {
        font-size: 32px;
        font-weight: 600;
        padding: 10px 0;
    }
    &_descr {
        margin: 10px auto 25px;
        font-weight: 300;
    }
    &_input {
        display: block;
        margin: 15px auto;
        background-color: transparent;
        padding: 15px 45px;
        border-bottom: 1px solid #fff;
        color: #fff;
    }
    &_btn {
        cursor: pointer;
        display: inline-block;
        margin: 25px auto 15px;
        font-size: 14px;
        text-transform: uppercase;
        line-height: 19px;
        font-weight: 600;
        letter-spacing: 1px;
        padding: 15px 45px;
        border: 1px solid #fff;
        background-color: transparent;
        color: #fff;
        transition: .3s all;

        &:hover {
            background: #a64b83;
            border: 1px solid #a64b83;
        }
    }
}


.close-popup {
    position: absolute;
    top: 15px;
    right: 15px;
    width: 23px;
    height: 23px;
    cursor: pointer;
}

.close-popup:before {
    content: '';
    background-color: #fff;
    position: absolute;
    height: 1px;
    width: 31px;
    top: 11px;
    left: -4px;
    transform: rotate(-45deg);
}

.close-popup:after {
    content: '';
    background-color: #fff;
    position: absolute;
    height: 1px;
    width: 31px;
    top: 11px;
    transform: rotate(45deg);
    left: -4px;
}

@media screen and (max-width: 768px) {
    .popup {
        padding-top: 160px;
        width: 100%;
        height: 100vh;
        font-size: 15px;
    }
    .close-popup {
        top: 35px;
    }
}
.success {
    display: none;
    color: #000;
    font-size: 22px;
    text-align: center;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 10;
    width: 100%;
    height: 100%;
    opacity: 1;
    text-transform: uppercase;
    font-weight: bold;
}

.success span {
    margin: auto;
}

.success span ~ * {
    transition: opacity 2s ease, filter 2s ease;
}

.success span ~ * span, .success span ~ * input, .success span ~ * textarea, .success span ~ * select {
    transition: opacity 2s ease, filter 2s ease;
}

.success span.active ~ * {
    opacity: .75;
    filter: blur(5px);
}

.success span.active ~ * span, .success span.active ~ * input, .success span.active ~ * textarea, .success span.active ~ * select {
    opacity: .75;
    filter: blur(5px);
}